@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #7c6e6d;
  margin: 10px 0;
}

#preview {
  display: inline-block;
}

.colorScheme, .editorWrap textarea, .previewWrap {
  background-color: #a89d9c;
  border: 1px solid black;
  box-shadow: 1px 1px 10px 2px #252120;
  border-top: none;
}

.toolbar {
  position: relative;
  background-color: #8bdbd7;
  padding: 4px 4px 3px 3px;
  border: 1px solid black;
  box-shadow: 1px 1px 10px 2px #252120;
  font-family: Russo One;
  font-size: 15px;
}

.toolbar i {
  width: 25px;
  color: black;
  margin-left: 5px;
}

.toolbar .fa-arrows-alt,
.toolbar .fa-compress {
  position: absolute;
  right: -5px;
}

.fa-compress:hover,
.fa-arrows-alt:hover {
  color: #5f8a92;
  cursor: pointer;
}

.editorWrap {
  width: 600px;
  margin: 18px auto;
}

.editorWrap .toolbar {
  width: 99%;
}

.editorWrap textarea {
  width: 99%;
  min-height: 200px;
  margin-bottom: -5px;
  resize: vertical;
  outline: none;
  padding-left: 5px;
  padding-top: 5px;
  font-size: 15px;
  color: #E7F0C3;
}

.previewWrap {
  width: 800px;
  margin: 20px auto;
  min-height: 200px;
  overflow-wrap: break-word;
  padding-right: 20px;
}

.previewWrap .toolbar {
  left: -1px;
  width: 100%;
  padding-right: 17px;
}

.previewWrap #preview {
  margin-left: 5px;
  margin-top: -10px;
  width: 100%;
  color: #E7F0C3;
}

@media screen and (max-width: 850px) {
  .previewWrap {
    width: 630px;
  }
  .editorWrap {
    width: 550px;
  }
}

.maximized {
  width: 96%;
  min-height: 100vh;
  margin: auto;
}

.maximized textarea {
  min-height: 95vh;
  resize: none;
}

.hide {
  display: none;
}

@media screen and (max-width: 650px) {
  body {
    margin: 5px 0;
  }
  .editorWrap {
    width: 80vw;
    margin: 0 auto;
  }
  .maximized {
    width: 95%;
    margin: auto;
  }
  .previewWrap {
    width: 95vw;
  }
  .previewWrap #preview {
    width: 100%;
  }
  .previewWrap #preview img {
    height: 200px;
  }
}

#preview blockquote {
  border-left: 3px solid #224B4B;
  color: #224B4B;
  padding-left: 5px;
  margin-left: 25px;
}

#preview code {
  background: white;
  padding: 1px 4px 2px 4px;
  font-size: 12px;
  font-weight: bold;
  color: black;
}

#preview pre {
  background: white;
  padding: 5px 0 5px 5px;
}

#preview h1 {
  border-bottom: 2px solid #37AFAA;
}

#preview h2 {
  border-bottom: 1px solid #37AFAA;
}

#preview table {
  border-collapse: collapse;
}

#preview td,
#preview th {
  border: 2px solid #37AFAA;
  padding-left: 5px;
  padding-right: 5px;
}
